
	<template>
		<div id="convention_select" class="convention_select">
			<div class="cons">
				<span class="con" v-for="con in conventions" v-text="con.short_name" @click="convention = con"></span>
			</div>
			<div class="buttons">
				<button class="button" @click="(() => {showConInfo = true; convention = {}})" >New Convention</button>
				<button class="button" v-if="convention.short_name" @click="showConInfo = true" >Update {{convention.short_name}}</button>	
			</div>	
			
			
		
			<div class="slideout con_info" :class="showConInfo? 'show' : '' ">
				<span class="title" >Convention Information for {{convention.short_name || 'new convention'}} </span>
				<div class="control_bar">
					<button type="button"  class="control_button fal fa-times"@click.prevent="showConInfo = false" ></button>	
					<button type="button" title="Clear con. form" class="control_button fal fa-redo-alt" @click="convention = {}"></button>
				</div>
				
				<div class="section_content">
					<form class="con_form" @submit="update_convention" >
						<div class="form_row">
							<div class="input_wrapper">
								<label>Convention Name</label>
								<input type="text" class="text_box" v-model="convention.name" />
							</div>
						</div>
						
						<div class="form_row">
							<div class="input_wrapper">
								<label>Con number</label>
								<input type="number" class="text_box" min="0" max="1000" v-model="convention.con_num" />
							</div>
						</div>
						<div class="form_row">
							<div class="input_wrapper">
								<label title="system-friendly version of the tag name">Con tag name</label>
								<input type="text" class="text_box" v-model="convention.tag_name" />
							</div>
						</div>
						<div class="form_row">
							<div class="input_wrapper">
								<label title="the con's common nickname">Con short name</label>
								<input type="text" class="text_box" v-model="convention.short_name" />
							</div>
						</div>
						<div class="form_row">
							<div class="input_wrapper">
								<label title="the date registration is open, typically the last day of, or the day after the prev con">Registration open date</label>
								<input type="date" class="text_box" v-model="convention.registration_open" />
							</div>
						</div>
						<div class="form_row">
							<div class="input_wrapper">
								<label alt="when online pre-registration closes">Registration close</label>
								<input type="text" class="text_box" v-model="convention.prereg_close" />
							</div>
						</div>
						<div class="form_row">
							<div class="form_element">
								<div class="input_wrapper">
									<input type="text" class="text_box" v-model="convention.start_date_time" />
								</div>
									<label class="below">Convention start date & time</label>
							</div>
							<div class="form_element">
								<div class="input_wrapper">
									<input type="text" class="text_box" v-model="convention.end_date_time" />
								</div>
									<label class="below">Convention end date & time</label>
							</div>
						</div>
						
						<div class="form_row">
							<div class="input_wrapper">
								<label title="event submissions open date & time">Event submissions open</label>
								<input type="text" class="text_box" v-model="convention.event_submissions_open" />
							</div>
						</div>
						<div class="form_row">
							<div class="input_wrapper">
								<label title="event submissions close date & time">Event submissions close</label>
								<input type="text" class="text_box" v-model="convention.event_submissions_close" />
							</div>
						</div>
						<div class="form_row">
							<div class="input_wrapper">
								<label title="event signups open date & time">Event signups open</label>
								<input type="text" class="text_box" v-model="convention.signups_open" />
							</div>
						</div>
						<div class="form_row">
							<div class="input_wrapper">
								<label title="event signups close date & time">Event signups close</label>
								<input type="text" class="text_box" v-model="convention.signups_close" />
							</div>
						</div>
						<div class="form_row">
							<div class="input_wrapper">
								<label>Convention Venue</label>
							<pre>SELECT OR CREATE</pre>
								<input type="text" class="text_box" v-model="convention.venue" />
							</div>
						</div>
						
						
						<div class="controls">
							<button type="submit" class="button">Submit</button>
						</div>
						
						
						
						
					</form>
				</div>
			
			</div>
			
				<article class="data_view" v-if="user.show_dev" @click="handle_data_view" >
					<span class="title">Conventions Data</span>
					<pre>{{JSON.stringify(conventions, null, 4)}}</pre>
				</article>
		</div>
		
	</template>

	<script>
		import Vue from 'vue'
		import { mapGetters } from 'vuex'
		
		export default{
			name: 'convention_select',
			
			data() {
				return {
					convention 		: {},
					showConInfo		: false,
				}
			},
			
			computed: {
				...mapGetters({
					conventions	: 'conventions',
					siteContent	: 'siteContent',
					user 		: 'user',
				})				
			},
			
			methods: {
				update_convention() {
					
					console.log('UPDATE CONVENTION FUNCTION NEEDED');
				}
				
			}
		}

	</script>	
	
	<style scoped>
		.convention_select {
			position: relative;
			display: flex;
			justify-content: space-between;
		}
		.convention_select .cons{
			display: flex;
			flex-direction: column;
			overflow: hidden;
			overflow-Y: auto;
			width: 10rem;
			height: 6rem;			
		}
		.convention_select .con {
			display: flex;
			width: 100%;
		}
		.convention_select .buttons{
			display: flex;
			width: 5rem;
			flex-diretion: column;
			justify-content: space-around;
		}
		
		/*	------	CON INFO SLIDEOUT	------	*/
		.slideout.con_info {
			background: var(--altBackground);
		}
		.slideout.con_info .form_row .input_wrapper {
			flex-wrap: nowrap;
		}
		.slideout.con_info label {
			text-align: right;
		}
		.slideout.con_info .text_box {
			border-radius: 0;
			background: none;
			font-size: 1.1rem;
			padding: 0 .5rem;
			color: var(--textColor2);
		}
		.con_form label.below {
			 width: 100%;
			-ms-flex-wrap: wrap;
			flex-wrap: wrap;
			font-size: .85rem;
			padding: 0 0 0 .5rem;
			-webkit-box-pack: start;
			-ms-flex-pack: start;
			justify-content: flex-start;
			-webkit-box-align: center;
			-ms-flex-align: center;
			align-items: center;
			border: 0;
		}
	
		.slideout.con_info {
			
		}
		.slideout.con_info {
			
		}
		.slideout.con_info {
			
		}
		.slideout.con_info {
			
		}
		
	</style>


