
	<template>
		<div id="scheduling_page">
		
		
		<span class="section_title">Scheduling Hub</span>
		
		
		<div class="sections">
			
			<section class="section scheduling_controls">
				<div class="con_controls">
					<convention_select/>
				</div>
				
				<div class="mini_report scheduling_report">
				
				</div>
			
			</section>
			
			
			
			
			
					<section class="section schedule_mini_report">				
						<div class="report_widgets">		
							
							<div class="report_widget event_breakdown">
								<span class="widget_title">Events Breakdown</span>
								<div  v-if="events" class="info_wrapper">
									<div class="info_element">
										<div class="element">
											<span class="label">Total Events for {{conventionInfo.short_name}}: </span>
											<span class="value"  v-text="events.length || 0 "></span>
										</div>
									</div>
									<div class="info_element summary">
								<div class="element">
									<span class="label"><span class="text">Waiting Approval</span><span class="colon">:</span> </span>
									<span class="value"  v-text="eventReport.waitingApproval || 0" ></span>
								</div>
								<div class="element">
									<span class="label"><span class="text">Pending Assignment</span><span class="colon">:</span> </span>
									<span class="value"  v-text="eventReport.pendingAssignment || 0" ></span>
								</div>
								<div class="element">
									<span class="label"><span class="text">Approved</span><span class="colon">:</span> </span>
									<span class="value"  v-text="eventReport.approved || 0" ></span>
								</div>
								<div class="element">
									<span class="label"><span class="text">Assigned</span><span class="colon">:</span> </span>
									<span class="value"  v-text="eventReport.assigned" ></span>
								</div>
									</div>	
								<div class="controls"></div>
									</div>	
								<div  v-else class="info">
									<span class="label">No Events for {{conventionInfo.short_name}}</span>
								</div>
							</div>					
							
							<div class="report_widget event_dates">
								<span class="widget_title">{{conventionInfo.short_name}} Scheduling Deadlines</span>
								<div class="info">
									<div class="info_element summary">
										<div class="row">
											<span class="label">Submissions Open: </span>
											<span class="date"  >{{conventionInfo.event_submissions_open | date_only}}</span>
										</div>
										<div class="row">
											<span class="label">Submissions Close: </span>
											<span class="date"  >{{conventionInfo.event_submissions_close|date_only}}</span>
										</div>
									</div>
									<div class="info_element summary">
										<div class="row">
											<span class="label">Event Signup Open: </span>
											<span class="date" >{{conventionInfo.signups_open|date_only}}</span>
										</div>
										<div class="row">
											<span class="label">Event Signup Close: </span>
											<span class="date" >{{conventionInfo.signups_close|date_only}}</span>
										</div> 
									</div>
								</div>
								<div class="controls"></div>
							</div>
							
						</div>
					</section>
		
		
		
		
		
		</div>
			<p>Scheduling Panel (create con, like admin panel)</p>
			<p>select con, show basic events info. and status. </p>
			<p>edit events, add events, schedule events in big grid</p>
			
			<p>{{events}}</p>
			
		
		</div>
	</template>
	
	
	
	<script>
		import Vue from 'vue'
		import { mapGetters } from 'vuex'
		import Router from 'vue-router'
		import convention_select from '@/components/includes/convention_select'
		
		Vue.use(Router);
		
		export default{
			name: 'scheduling_page',
			
			
			data() {
				return {
					eventReport: {},
				}
			},
			
			components: {
				'convention_select'	: convention_select,
			},
			
			
			watch: {
				
				
			},
			
			created() {
				var vm = this;
				vm.check_user();
				vm.get_store_data();
			},
			
			computed: {
				...mapGetters({
					user			: 'user',
					events			: 'events',
					conventionInfo	: 'conventionInfo',
				}),
				
				
			},
			
			
			mounted: function() {
				var vm = this;
				vm.get_events_report();
				
			},
			
			
			methods: {
				//GET STORE DATA
				get_store_data() {
					var vm = this;
					vm.$store.dispatch('get_events').then(()=>{});
				},
				
				check_user() {
					var vm = this;
					vm.user.permissions.scheduling >= 10? '' : vm.$router.go('/');  
				},
				
				get_events_report() {
					var vm = this;
					
					vm.eventReport.rejected 		=0;
					vm.eventReport.cancelled 		=0;
					vm.eventReport.waitingApproval	=0;
					vm.eventReport.approved 		=0;
					vm.eventReport.scheduled 		=0;
					vm.eventReport.pendingAssignment=0;
					vm.eventReport.hasTable 		=0;
					vm.eventReport.assigned 		=0;
					
					if(vm.events.length > 0) {
						vm.events.forEach(function(thisEvent){
							/*	event status: 	0 = unapproved, 20 = approved, 
												30 = pendingAssignment , 31 = scheduled, 32 = has Location,   
												40 = assigned, 90 = rejected, 91 = canceled
							*/
							switch(thisEvent.status) {									
								case 20: 
									vm.eventReport.approved ++;
									break;
									
								case 30: 
									vm.eventReport.pendingAssignment ++;
									break;
									
								case 31: 
									vm.eventReport.scheduled ++;
									break;
									
								case 32: 
									vm.eventReport.hasTable ++;
									break;
									
								case 40: 
									vm.eventReport.assigned ++;
									break;
								
								case 90: 
									vm.eventReport.rejected ++;
									break;
									
								case 91: 
									vm.eventReport.cancelled ++;
									break;
									
								default: 
									vm.eventReport.unapproved ++;
							}
							
						});
					}	
				}
			}
			
		}
	</script>
	
	
